import { Layout, Menu } from 'antd';
import { TrophyFilled, YoutubeFilled, HomeFilled } from '@ant-design/icons';
import React, { Component } from 'react';
import mainlogo from '../Images/3x3logomain.png'
import '../CSS/Navibar.css';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import PlayerRanking from './PlayerRanking';
import Result from './Result';
import VideoClip from './VideoClip';
import Homepage from './Homepage';
import About3x3 from './About3x3';

const {  Content,  Sider } = Layout;


class Navibar extends Component {

  render() {
    return (
      <Router basename="/react_study">

        <Layout style={{ height: "100vh", backgroundColor: 'white' }}>



          <Sider
            breakpoint="lg"
            collapsedWidth="0"
            onBreakpoint={broken => {
              console.log(broken);
            }}
            onCollapse={(collapsed, type) => {
              console.log(collapsed, type);
            }}
            style={{ backgroundColor: 'white' }}
          >

            {/* main logo */}
            <div style={{ borderStyle: 'none', width: '200px', height: '80px', marginTop: '30px' }}>
              <a href="/"><img alt="" src={mainlogo} style={{ width: '200px', height: '80px' }} /></a>
            </div>


            <div className="logo" />
            <Menu theme="white" mode="inline" defaultSelectedKeys={['1']} style={{ marginTop: '100px' }}>
              <Menu.Item key="1" icon={<HomeFilled />}>
                <Link className="linkitem" to="/">Homepage</Link>
              </Menu.Item>
              <Menu.Item key="2" icon={<TrophyFilled />}>
                <Link className="linkitem" to="/result">Match Result</Link>
              </Menu.Item>
              <Menu.Item key="3" icon={<TrophyFilled />}>
                <Link className="linkitem" to="/playerranking">Player Rank</Link>
              </Menu.Item>
              <Menu.Item key="4" icon={<YoutubeFilled />}>
                <Link className="linkitem" to="/videoclip">Video Clip</Link>
              </Menu.Item>
              <Menu.Item key="5" icon={<YoutubeFilled />}>
                <Link className="linkitem" to="/about3x3">About 3x3</Link>
              </Menu.Item>
            </Menu>
          </Sider>
          <Layout>
            
            <Content style={{ margin: '24px 16px 0' }}>
              <div>
                <Route exact path='/' component={Homepage} />
                <Route path='/result' component={Result} />
                <Route path='/playerranking' component={PlayerRanking} />
                <Route path='/videoclip' component={VideoClip} />
                <Route path='/about3x3' component={About3x3} />
              </div>
            </Content>
          </Layout>
        </Layout>




      </Router>

    )
  }
}


export default Navibar;